.header{
    display: flex;
    justify-content: space-between;
    padding: 14px 108px;
    background-color: #D1E0D8;
    font-family: Manrope;
}

.navbar{
    display: flex;
    align-items: center;
    gap: 32px;
}

.list{
    gap: 32px;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.link{
    color: #374F42;
    font-weight: 700;
font-size: 12px;
}

.button{
    border: none;
    border-radius: 64px;
padding: 12px 16px;
width: 100px;
height: 46px;
box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
background: #649179;
color: #FFFFFF;
}